import Header from "../../components/Header";
import { useLoaderData } from "@remix-run/react";

export async function loader({ params }) {
  const slug = params.slug;
  // Use the WordPress API endpoint for pages
  const wordpressApiUrl = `http://localhost/remix-project/wp-json/wp/v2/pages?slug=${slug}`;

  try {
    const response = await fetch(wordpressApiUrl);
    if (!response.ok) {
      throw new Error("Failed to fetch pages from WordPress.");
    }
    const pages = await response.json();
    return pages.length > 0 ? pages[0] : null; // Return the first page or null if not found
  } catch (error) {
    console.error("Error fetching WordPress pages:", error);
    return null; // Return null in case of error
  }
}

export default function Slug() {
  const page = useLoaderData();

  if (!page) {
    return (
      <div>
        <Header title="Page Not Found" />
        <main className="bg-gray-100 container mx-auto mt-6 p-6 rounded-lg">
          <h1 className="text-4xl">Page not found</h1>
          <p>The requested page could not be found.</p>
        </main>
      </div>
    );
  }

  return (
    <div>
      <Header title={page.title.rendered} />
      <main className="bg-gray-100 container mx-auto mt-6 p-6 rounded-lg">
        <h1 className="text-4xl">{page.title.rendered}</h1>
        <div className="text-2xl mt-4">{new Date(page.date).toLocaleDateString()}</div>
        <article className="mt-4 space-y-2" dangerouslySetInnerHTML={{ __html: page.content.rendered }}></article>
      </main>
    </div>
  );
}
